<template>
  <h1 class="my-5">User login</h1>
  <form @submit.prevent="processForm">
    <input
      class="form-control my-2"
      type="email"
      placeholder="email"
      v-model.trim="email"
    />
    <input
      class="form-control my-2"
      type="password"
      placeholder="password"
      v-model="password"
    />
    <button
      class="btn btn-primary btn-block"
      type="submit"
      :disabled="isSumbitBlocked"
    >
      Login
    </button>
  </form>
</template>

<script>
import { mapActions } from "vuex";
export default {
  data() {
    return {
      email: "",
      password: "",
    };
  },
  computed: {
    isSumbitBlocked() {
      if (!this.email.includes("@")) {
        return true;
      }
      if (this.password.length > 5) {
        return false;
      }
      return true;
    },
  },
  methods: {
    ...mapActions(["userLogin"]),
    processForm() {
      this.userLogin({ email: this.email, password: this.password });
      this.email = "";
      this.password = "";
    },
  },
};
</script>